import styled from 'styled-components';
import { NavLink } from 'react-router-dom';

const MainPage = ()=>{

  return(<>
    <S.Wrapper>
      <div className='mainPageBlock1'>
        <h3>Услуги</h3>
        <div className='mainPageBlock1Section'>
          <div className='mainPageBlock1Section1Button'><NavLink to='/fundament'><h4>Фундамент</h4></NavLink></div>
          <div className='mainPageBlock1Section1Button'><NavLink to='/roofing'><h4>Кровельные работы</h4></NavLink></div>
          <div className='mainPageBlock1Section1Button'><NavLink to='/turnkeysaunas'><h4>Бани под ключ</h4></NavLink></div>
        </div>
      </div>
      {/* <div className='mainPageBlock2'>
        <h3>Дополнительные услуги</h3>
        <div className='mainPageBlock2Section'>
          <div className='mainPageBlock2Section1Button'><NavLink to='/fundamentPage'><h4>Фундамент</h4></NavLink></div>
          <div className='mainPageBlock2Section1Button'><NavLink to='/fundamentPage'><h4>Кровельные работы</h4></NavLink></div>
          <div className='mainPageBlock2Section1Button'><NavLink to='/turnkeysaunas'><h4>Бани под ключ</h4></NavLink></div>
        </div>
      </div> */}
      
    </S.Wrapper>
  </>)
};

export default MainPage;

const S = {
  Wrapper: styled.div`
  display: flex;
  justify-content: space-around;
  .mainPageBlock1, .mainPageBlock2 {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    > h3 {
      margin: 0;
      padding: 0;
    }
    a > h4 {
      margin: 0;
      padding: 0;
    }
  }
  .mainPageBlock1Section, .mainPageBlock2Section {
    display: flex;
    flex-wrap: wrap;
    a {
      text-decoration: none;
    }
  }
  .mainPageBlock1Section1Button, .mainPageBlock2Section1Button {
    margin: 0 10px;
  }
  `
}