import styled from 'styled-components';
import headerImg from '../../assets/headerImg2.jpg';

const TurnkeySaunas = ()=>{

  return (<>
      <S.Section1>
        <div className='headerBanerIn'>
            <h1>Строительство бань по Гомелю и области</h1>
            <h2>Без предоплаты</h2>
            <h2>Под ключ</h2>
            <div className='cardContainer' >
              
            </div>
            <button className='btnHederBanner'>Связаться</button>
          </div>
      </S.Section1>
      
      <S.Section2>
      <div className='productTitle'>
        <p>БАНИ из:</p>
      </div>
      <div className='productContainer'>
        
      </div>
      <div className='produstBtn'>
        <button>Получить консультацию</button>
      </div>
    </S.Section2>
  </>)
};

export default TurnkeySaunas;

const S = {
  Section1: styled.section`
    width: 100%;
    background: url(${headerImg}) 50% 50% / contain no-repeat;
    position: relative;
    :after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba(0,0,0,.65);
      z-index: 2;
    }
    .headerBanerIn {
      position: relative;
      z-index: 3;
      color: #fff;
      padding: 50px;  
    }
    .cardContainer {
      display: flex;
      flex-wrap: wrap;
      align-items: flex-start;
    }
  `,
  Section2: styled.section`
    width: 100% ;
    display: flex;
    flex-direction: column;
    align-items: center;
    .productContainer {
      display: flex;
      justify-content: center;
    }
  `
}